.shortsVideoScreen {
    //margin: 40px 0px !important;
    margin-bottom: 100px;
    margin-top: 40px;
    min-height: 900px;

    @media screen and (max-width: 576px) {
        margin-top: 10px;
        margin-bottom: 30px;
    }

    article {

        figure {
            float: left;
            margin-right: 3rem;

            @media screen and (max-width:768px) {
                margin-right: 0px;
            }
        }

        .title {
            font-weight: 900;
            font-size: 36px;
        }

        .tags {
            margin: 10px 0px;
            font-size: 20px;
            color: black;
            font: normal normal normal 20px/27px Helvetica Neue;
        }

        .text {
            font-size: 24px;
            line-height: 36px;

            ul {
                li {
                    &::marker {
                        margin-left: 20px;
                        font-weight: bold;
                        content: disc;
                    }
                }
            }
        }

    }
}